export default {
	data() {
		return {
			loadStatus: "more",
			pageNum: 1,
			pageSize: 10,
			total: "",
			list: [],
			searchVal: "",
			queryStatus:0,//0-所有  1-已完成  2-未完成
			statusArr: [
				"全部",
				"已完成",
				"未完成"
			],
			status: "按状态筛选"
		}
	},
	onLoad() {
		this.getList(1);
	},
	methods: {
		getList(startPage) {
			const that = this;
			let param = {
				dynamicParam: that.searchVal,
				queryStatus:that.queryStatus,
				startPage: startPage,
				pageSize: that.pageSize
			}
			that.loadStatus = "loading";
			that.$api.auctionList(param).then(res => {
				if (res.success) {
					let result = res.datas.rows;
					for (var i in result) {
						result[i].memo = result[i].memo?result[i].memo:"暂无";
						that.list.push(result[i]);
					}
					let total = res.datas.total;
					that.total = total;
					let totalPageNum = Math.ceil(total / that.pageSize);
					console.log(totalPageNum);
					if (parseInt(totalPageNum) > parseInt(that.pageNum)) {
						that.pageNum++;
						that.loadStatus = "more";
					} else {
						that.loadStatus = "noMore";
					}
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		clearList() {
			const that = this;
			that.pageNum = 1;
			that.total = "";
			that.list = [];
		},
		search() {
			const that = this;
			that.clearList();
			that.getList(1);
		},

		statusPicker: function(e) {
			const that = this;
			that.status = that.statusArr[e.detail.value];
			that.queryStatus = e.detail.value;
			that.clearList();
			that.getList(1);
		},
		gotoProcess(id){
			const that =this;
			that.$Router.push({
				path:"/pages/my/manageNavs/process/index",
				query:{
					id:id
				}
			})
		},
		gotoDetail(id) {
			const that = this;
			that.$Router.push({
				path: "/pages/my/manageNavs/hasJoin/detail",
				query: {
					id: id
				}
			})
		},
	},
	onReachBottom() {
		const that = this;
		if (that.loadStatus == "noMore") {
			return;
		} else if (that.pageNum != 1) {
			that.getList(that.pageNum);
		}
	},
}
